.postimg{
    width: 100%;
    height: 450px;
}
.authordetails {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 15px;
}

.authorinfo img {
    margin-right: 24px;
    width: 60px;
    height: 60px;
}

.authorinfo {
    display: flex;
    align-items: center;
}

.authorname {
    color: #111111;
    font-size: 24px;
    font-family: 'Exo 2', sans-serif;
    font-weight: 700;

}

.postdate {
    color: rgba(17, 17, 17, 0.60);
    font-size: 16px;
    font-family: 'Exo 2', sans-serif;
    font-weight: 600;
}

.postinfo {
    display: flex;
    align-items: center;
}

.postinfo p {
    margin-right: 8px;
}

.readtime {
    color: rgba(17, 17, 17, 0.60);
    font-size: 20px;
    font-family: 'Exo 2', sans-serif;
    font-weight: 500;
}

.bokmaricon {
    width: 15px;
    height: 18px;
}

.posttitle {
    color: #111111;
    font-size: 40px;
    font-family: 'Exo 2', sans-serif;
    font-weight: 700;
    line-height: 40px;
    margin: 0;
    margin-top: 10px;
}

.posttag {
    color: rgba(17, 17, 17, 0.60);
    font-size: 20px;
    font-family: 'Exo 2', sans-serif;
    font-weight: 500;
    margin: 0;
    margin-top: 15px;
}

.markread {
    color: #6047EC;
    font-size: 20px;
    font-family: 'Exo 2', sans-serif;
    font-weight: 600;
    text-decoration: underline;
}
.posthr{
    margin-bottom: 40px;
}